import React, { Component } from "react";
import { Mutation } from "react-apollo";
import gql from "graphql-tag";
import Form from "./styles/Form";
import formatMoney from "../lib/formatMoney";

/**
 * In order to send a form request to add an item, first we need to specify our action
 *  draft out mutation action with the required fields and type
 *      Then assign the specified fields to those variable [title : $title]
 *          Once that's done, it will return the id
 *
 */
const CREATE_ITEM_MUTATION = gql`
	mutation CREATE_ITEM_MUTATION(
		$title: String!
		$description: String!
		$price: Int!
		$image: String
		$largeImage: String
	) {
		createItem(
			title: $title
			description: $description
			price: $price
			image: $image
			largeImage: $largeImage
		) {
			id
		}
	}
`;

export class CreateItem extends Component {
	state = {
		title: "hmm",
		description: "hmm",
		image: "hmm",
		largeImage: "hmm",
		price: "22",
	};

	//by doing it this way, it will now work with every input field instead of writing seperate methods for each input onChange event
	handleChange = (e) => {
		const { name, type, value } = e.target;
		const val = type === "number" ? parseFloat(value) : value;
		this.setState({ [name]: val });
		// console.log(e.target.value);
		// this.setState({ title: e.target.value });
	};

	render() {
		return (
			<Mutation query={CREATE_ITEM_MUTATION} variables={this.state}>
				{(createItem, { loading, error, called, data }) => (
					<Form
						onSubmit={(e) => {
							e.preventDefault();
							console.log(this.state);
						}}
					>
						<fieldset>
							<label htmlFor="title">
								Title
								<input
									type="text"
									id="title"
									name="title"
									placeholder="Title"
									required
									onChange={this.handleChange}
									value={this.state.title}
								/>
							</label>

							<label htmlFor="title">
								price
								<input
									type="number"
									id="price"
									name="price"
									placeholder="price"
									required
									onChange={this.handleChange}
									value={this.state.price}
								/>
							</label>

							<label htmlFor="title">
								Description
								<input
									id="description"
									name="description"
									placeholder="Enter a Description"
									required
									onChange={this.handleChange}
									value={this.state.description}
								/>
							</label>
							<button type="submit">Submit</button>
						</fieldset>
					</Form>
				)}
			</Mutation>
		);
	}
}

export default CreateItem;
export { CREATE_ITEM_MUTATION };
